.layer-rocket .layer-top { display: flex; justify-content: center; background: url("../images/bg-cloud.png") no-repeat center 15vw; background-size: cover; transition: all 1s; margin-bottom: -5px; height: 28vw; z-index: 100; }

.layer-rocket .layer-top.no-cloud { background-position: center -300%; }

.layer-rocket .layer-cont { width: 510px; max-height: 100vh; background-color: #fff; overflow-x: hidden; overflow-y: auto; transition: all 0.5s; opacity: 1; border-radius: 60px; transition: transform 1s; transform: translateY(120%); }

.layer-rocket .layer-cont.w600 { width: 600px; }

.layer-rocket .layer-cont::-webkit-scrollbar { width: 4px; height: 4px; }

.layer-rocket .layer-cont::-webkit-scrollbar-thumb { transition: all .5s; }

.layer-rocket .layer-cont .layer-cont-in { color: #000; padding-bottom: 61px; opacity: 1; }

.layer-rocket .layer-cont.active { transform: translateY(0); }

.layer-rocket .btn-rocket-open { position: absolute; top: 0vw; left: 50%; margin-left: -6vw; background-color: transparent; border: 0; width: 12vw; display: none; }

.layer-rocket .btn-rocket-open img { width: 100%; }

.layer-rocket .btn-rocket-open.open { animation: slide-out-top 0.5s cubic-bezier(0.55, 0.085, 0.68, 0.53) both; }

.layer-rocket .btn-rocket-open.close { animation: slide-in-blurred-bottom 0.6s cubic-bezier(0.23, 1, 0.32, 1) both; }

.layer-rocket .btn-rocket-close { position: absolute; top: 100px; box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2); left: 50%; margin-left: -20px; background: url("../images/rocket-close.png") no-repeat center; background-size: 70%; background-color: #fff; border-radius: 100%; width: 40px; height: 40px; border: 0; font: 0/0 a; transform: translateY(1000px) scaleY(2.5) scaleX(0.2); transform-origin: 50% 100%; filter: blur(40px); opacity: 0; z-index: 1; }

.layer-rocket .btn-rocket-close.open { animation: slide-in-blurred-bottom 0.6s cubic-bezier(0.23, 1, 0.32, 1) both; }

.layer-rocket .btn-rocket-close.close { opacity: 0; animation: slide-out-blurred-bottom 0.45s cubic-bezier(0.755, 0.05, 0.855, 0.06) both; }

.layer-rocket .benefitView { position: relative; width: 100%; height: 100%; padding: 0 8%; }

.layer-rocket .benefitView .benefitView-top { border-bottom: 2px solid #c9c9c9; height: 168px; display: flex; justify-content: center; align-items: center; position: relative; }

.layer-rocket .benefitView .benefitView-top dt { text-align: center; }

.layer-rocket .benefitView .benefitView-top dt img { width: 70%; }

.layer-rocket .benefitView .benefitView-top dd { position: absolute; bottom: 8px; right: 0; font-size: 12px; letter-spacing: -0.24px; line-height: 1.4; font-family: 'Noto Sans KR bold'; color: #575757; }

.layer-rocket .benefitView .benefitView-cont { margin-top: 20px; }

.layer-rocket .benefitView .benefitView-cont > dl { text-align: left; font-family: 'Noto Sans KR black'; line-height: 1.6; }

.layer-rocket .benefitView .benefitView-cont > dl dt { background: linear-gradient(to right, #00cdff, #7d14ff); color: transparent; -webkit-background-clip: text; font-family: 'Noto Sans KR black', sans-serif; font-size: 24px; letter-spacing: -0.48px; }

.layer-rocket .benefitView .benefitView-cont > dl dd { margin-top: 20px; font-size: 16px; line-height: 25px; letter-spacing: -0.32px; font-family: 'Noto Sans KR bold'; color: #000000; }

.layer-rocket .benefitView .benefitView-cont > dl dd p { margin-top: 10px; }

.layer-rocket .benefitView .benefitView-cont > dl dd p:first-child { margin-top: 0; }

.layer-rocket .benefitView .benefitView-cont .arrowBtn { width: 249px; margin-left: 0; text-align: center; }

.layer-rocket .benefitView > div { text-align: center; }

.layer-rocket .benefitView > div dl dt { text-align: center; }

.layer-rocket .benefitView > div dl dd { text-align: center; }

.layer-rocket .imageFileView { padding: 40px 8%; }

.layer-rocket .imageFileView > div { display: none; }

.layer-rocket .imageFileView > div dl { margin-bottom: 30px; }

.layer-rocket .imageFileView > div dl dt { background: linear-gradient(to right, #00cdff, #7d14ff); color: transparent; -webkit-background-clip: text; font-family: 'Noto Sans KR black', sans-serif; font-size: 14px; line-height: 1.4; letter-spacing: -0.28px; }

.layer-rocket .imageFileView > div dl dd { margin-top: 10px; font-size: 24px; line-height: 1.6; letter-spacing: -0.48px; color: #000000; font-family: 'Noto Sans KR black'; }

.layer-rocket .imageFileView > div div { text-align: center; position: relative; }

.layer-rocket .imageFileView > div div img { width: 200px; height: auto; }

.layer-rocket .imageFileView > div.step02 .btnWrap { display: flex; margin-top: 31px; }

.layer-rocket .imageFileView > div.step02 .btnWrap .goBtn { background-color: #575757; text-align: center; }

.layer-rocket .imageFileView > div.step02 .btnWrap .goBtn:first-child { background-color: #6414ff; width: 40%; margin-right: 4px; }

.layer-rocket .imageFileView > div.step03 { padding-top: 60%; padding-bottom: 100px; background: url("../images/btn-check.png") no-repeat 0 0; background-position: center 20%; }

.layer-rocket .imageFileView > div.step03 p { background: linear-gradient(to right, #00cdff, #7d14ff); color: transparent; -webkit-background-clip: text; font-family: 'Noto Sans KR black', sans-serif; font-size: 40px; line-height: 1.2; letter-spacing: -0.8px; text-align: center; }

.layer-rocket .imageFileView .on { display: block; }

.layer-rocket .luckyView { position: relative; width: 100%; height: 100%; }

.layer-rocket .luckyView .luckyView-top { background: url("../images/luckyView-particle.png") no-repeat center 5vw; background-size: contain; margin-top: 47px; }

.layer-rocket .luckyView .luckyView-top .sub-tit { font-size: 20px; line-height: 1.6; letter-spacing: -0.4px; font-family: 'Noto Sans KR bold'; color: #000; text-align: center; }

.layer-rocket .luckyView .luckyView-top h4 { font-size: 60px; line-height: 1.5; letter-spacing: -1.2px; font-family: 'Noto Sans KR bold'; text-align: center; color: #3617ce; margin-top: 10px; }

.layer-rocket .luckyView .luckyView-top .cover { padding: 0 8%; display: flex; align-items: center; justify-content: center; }

.layer-rocket .luckyView .luckyView-top .cover img { width: 80%; max-width: 400px; }

.layer-rocket .luckyView .luckyView-top ul { margin-top: 28.6px; }

.layer-rocket .luckyView .luckyView-top ul li { font-size: 20px; line-height: 1.6; letter-spacing: -0.4px; font-family: 'Noto Sans KR bold'; text-align: center; }

.layer-rocket .luckyView .luckyView-top ul li + li { margin-top: 10px; }

.layer-rocket .luckyView .luckyView-list { padding: 0 8%; }

.layer-rocket .luckyView .luckyView-list .list-tit { background: url("../images/luckyView-ribbon.png") center center no-repeat; background-size: 327px; width: 327px; height: 67.5px; margin: 50.5px auto 13px; text-align: center; display: flex; align-items: center; justify-content: center; box-sizing: border-box; }

.layer-rocket .luckyView .luckyView-list .list-tit span { display: block; font-size: 24px; line-height: 1.6; letter-spacing: -0.48px; font-family: 'Noto Sans KR black'; color: #fff; position: relative; top: -5px; }

.layer-rocket .luckyView .luckyView-list .list-conts > ul { flex-wrap: wrap; display: flex; width: 440px; margin: 0 auto; }

.layer-rocket .luckyView .luckyView-list .list-conts > ul li { width: 210px; box-sizing: border-box; background: url("../images/luckyView-bg-stars.png") no-repeat center center; background-size: cover; border-radius: 30px; overflow: hidden; margin-top: 20px; }

.layer-rocket .luckyView .luckyView-list .list-conts > ul li:nth-child(odd) { margin-right: 10px; }

.layer-rocket .luckyView .luckyView-list .list-conts > ul li:nth-child(even) { margin-left: 10px; }

.layer-rocket .luckyView .luckyView-list .list-conts .cont-in { display: flex; align-items: center; justify-content: center; flex-flow: column; padding: 30px 0; }

.layer-rocket .luckyView .luckyView-list .list-conts .cont-in .cover { padding: 0 30px; }

.layer-rocket .luckyView .luckyView-list .list-conts .cont-in .cover img { width: 100%; }

.layer-rocket .luckyView .luckyView-list .list-conts .cont-in .infos { padding: 0 5px; margin-top: 30px; text-align: center; font-size: 20px; line-height: 1.6; letter-spacing: -0.4px; font-family: 'Noto Sans KR bold'; word-break: break-all; word-wrap: break-word; color: #fff; line-height: 1.4; }

.layer-rocket.active { opacity: 1; display: flex; flex-direction: row; align-items: center; justify-content: center; z-index: 55; }

.layer-rocket.low-zIndex { z-index: 45; }

_:-ms-fullscreen, :root .layer-rocket .layer-cont { max-height: 700px; }

body.fixed { overflow: hidden; position: fixed; }

.modal-layer { position: fixed; display: none; z-index: 65; width: 100%; left: 0; max-width: 480px; }

.modal-layer .modal-inner { overflow: hidden; border-radius: 10px; background-color: #f4f4f4; margin: 15px; position: relative; }

.modal-layer .modal-cont { position: relative; box-sizing: border-box; }

.modal-dimmed { position: fixed; left: 0; top: 0; width: 100%; height: 100%; background-color: #000; font: 0/0 a; opacity: 0.5; z-index: 60; display: none; }

/*티켓레이어 수정*/
#content.ticketInfo .ticketAniWrap { display: block; background-color: transparent; overflow: hidden; transform: translateY(120%); transition: transform 1s; }

#content.ticketInfo .ticketAniWrap.on { transform: translateY(0); }

#content.ticketInfo .ticketAniWrap .ticketAni { overflow-x: hidden; overflow-y: auto; height: calc(100% - 70px); }

#content.ticketInfo .ticketAniWrap .ticketAni .ticketing.add-svg { padding-bottom: 0; background: none; }

#content.ticketInfo .ticketAniWrap .ticketAni .ticketing.add-svg .svg-W { margin: 0 auto; max-width: 500px; height: 500px; overflow: hidden; }

#content.ticketInfo .ticketAniWrap .ticketAni .winning.add-svg { height: 100%; }

#content.ticketInfo .ticketAniWrap .ticketAni .winning.add-svg .svg-W { display: flex; align-items: center; justify-content: center; position: absolute; width: 100%; top: 50%; left: 50%; height: 100%; transform: translate(-50%, -50%); overflow: hidden; }

#content.ticketInfo .ticketAniWrap .ticketAni .winning.add-svg .svg-W > div { transform: scale(1.6); transform-origin: center; }

#content.ticketInfo .ticketAniWrap .ticketAni .winning.add-svg .svg-cont { position: relative; }

#content.ticketInfo .ticketAniWrap .ticketAni.on { animation: none; }

#content.ticketInfo .ticketAniWrap .ticketAni.on .ticketing.add-svg.on { animation: none; background-color: #fff; border-radius: 40px 40px 0 0; }

#content.ticketInfo .ticketAniWrap .ticketAni.on .issuanceTicket.on { animation-delay: .3s; background-color: #fff; border-radius: 40px 40px 0 0; padding-bottom: 40px; }

#content.ticketInfo .ticketAniWrap .ticketAni.on .winning.on { background-color: #fff; border-radius: 40px 40px 0 0; padding-bottom: 40px; }

#content.ticketInfo .ticketAniWrap .ticketAni.on .winning.on::before { display: none; }

#content.ticketInfo .ticketAniWrap .closeBtn { top: 23px; }

/*add svg*/
.ticket .ticketParticipate ul li.step01 .add-svg { background-image: none; padding: 0; }

.ticket .ticketParticipate ul li.step01 .add-svg .svg-W { padding: 0; width: 116%; }

.ticket .ticketParticipate ul li.step03 .add-svg { background-image: none; padding: 0; }

.ticket .ticketParticipate ul li.step03 .add-svg .svg-W { margin: 70px 0; padding: 0; width: 116%; margin: 5% 0 5% -8%; }

.delivery .deliveryStep > ul > li > dl.step01.add-svg { background-image: none; padding: 0; }

.delivery .deliveryStep > ul > li > dl.step01.add-svg .svg-W { padding: 0; width: 116%; margin: -15% 0 -15% -8%; }

.delivery .deliveryStep > ul > li > dl.step03.add-svg { background-image: none; padding: 0; }

.delivery .deliveryStep > ul > li > dl.step03.add-svg .svg-W { padding: 0; width: 116%; margin: -20% 0 -20% -8%; }

.delivery .deliveryStep > ul > li > dl.step04.add-svg { background-image: none; padding: 0; }

.delivery .deliveryStep > ul > li > dl.step04.add-svg .svg-W { padding: 0; width: 116%; margin: -20% 0 -30% -8%; }

.mainTab .tabContent ul .leftTab ul li:first-child .add-svg .svg-W, .mad .madPass > ul li:first-child .add-svg .svg-W, .partners .listPartners > ul li:first-child .add-svg .svg-W { position: absolute; top: 8px; right: 40px; width: 230px; }

.mainTab .tabContent ul .leftTab ul li:first-child .add-svg::after, .mad .madPass > ul li:first-child .add-svg::after, .partners .listPartners > ul li:first-child .add-svg::after { display: none; }

.mainTab .tabContent ul .leftTab ul li:last-child .add-svg .svg-W, .mad .madPass > ul li:last-child .add-svg .svg-W, .partners .listPartners > ul li:last-child .add-svg .svg-W { position: absolute; top: -30px; right: 30px; width: 240px; }

.mainTab .tabContent ul .leftTab ul li:last-child .add-svg::after, .mad .madPass > ul li:last-child .add-svg::after, .partners .listPartners > ul li:last-child .add-svg::after { display: none; }

.ticket .ticketRewards ul li:first-child.add-svg { overflow: hidden; }

.ticket .ticketRewards ul li:first-child.add-svg::before { display: none; }

.ticket .ticketRewards ul li:first-child.add-svg .svg-W { position: absolute; top: -110px; right: -50px; width: 300px; }

.ticket .ticketRewards ul li:last-child.add-svg { overflow: hidden; }

.ticket .ticketRewards ul li:last-child.add-svg::before { display: none; }

.ticket .ticketRewards ul li:last-child.add-svg .svg-W { position: absolute; top: -150px; right: -75px; width: 350px; }

@keyframes slide-out-top { 0% { transform: translateY(0);
  opacity: 1; }
 100% { transform: translateY(-1000px);
  opacity: 0; } }

@keyframes slide-in-blurred-bottom { 0% { transform: translateY(1000px) scaleY(2.5) scaleX(0.2);
  transform-origin: 50% 100%;
  filter: blur(40px);
  opacity: 0; }
 100% { transform: translateY(0) scaleY(1) scaleX(1);
  transform-origin: 50% 50%;
  filter: blur(0);
  opacity: 1; } }

@keyframes slide-out-blurred-bottom { 0% { transform: translateY(0) scaleY(1) scaleX(1);
  transform-origin: 50% 50%;
  filter: blur(0);
  opacity: 1; }
 100% { transform: translateY(1000px) scaleY(2) scaleX(0.2);
  transform-origin: 50% 100%;
  filter: blur(40px);
  opacity: 0; } }

