
/* 컴파일되는
	* 여러 줄
	* 주석 */
// variable
$frontColor: #00cdff;
$backColor: #7d14ff;
$black: #000000;
$white: #ffffff;
$bgColor: #0d1c2f;
$bgColor02: #6414ff;
$bgColor03: #171718;
$bgColor04: #0e1c30;
$bgColor05: #5e45d8;
$bgColor06: #1a1a1a;
$lightGray: #575757;
$wrongColor: #ff404e;
$point: #ff404e;
$darkGray: #c9c9c9;
$mainColor: #3617ce;
$oneColor: #8674e2;


//mixin
@mixin NotoSans () {
	font-family: 'Noto Sans KR', sans-serif; }

@mixin colorGradation() {
	color: $white;
	font-family: 'Noto Sans KR black', sans-serif; }

@mixin borderGradient() {
	background-color: rgba(54, 23, 206, 0.24);
	border: 1px solid $mainColor; }

@mixin font12() {
	font-size: 12px;
	letter-spacing: -0.24px;
	line-height: 1.4;
	font-family: 'Noto Sans KR bold'; }
@mixin font14() {
	font-size: 14px;
	letter-spacing: -0.28px;
	line-height: 1.4;
	font-family: 'Noto Sans KR bold'; }
@mixin font16Gradation() {
	@include colorGradation();
	font-size: 16px;
	line-height: 25px;
	letter-spacing: -0.32px; }
@mixin font16() {
	font-size: 16px;
	line-height: 25px;
	letter-spacing: -0.32px;
	font-family: 'Noto Sans KR bold'; }
@mixin font20() {
	font-size: 20px;
	line-height: 1.6;
	letter-spacing: -0.4px;
	font-family: 'Noto Sans KR bold'; }
@mixin font24Gradation() {
	@include colorGradation();
	font-size: 24px;
	line-height: 1.6;
	letter-spacing: -0.48px; }
@mixin font24() {
	font-size: 24px;
	line-height: 1.6;
	letter-spacing: -0.48px;
	font-family: 'Noto Sans KR bold'; }
@mixin font28() {
	font-size: 28px;
	line-height: 1.6;
	letter-spacing: -0.56px;
	font-family: 'Noto Sans KR bold'; }
@mixin font44() {
	@include colorGradation();
	font-size: 44px;
	line-height: 1.1;
	letter-spacing: -0.88px; }
@mixin font60() {
	font-size: 60px;
	line-height: 1.5;
	letter-spacing: -1.2px;
	font-family: 'Noto Sans KR bold'; }
@mixin font80() {
	@include colorGradation();
	font-size: 80px;
	line-height: 1.3;
	letter-spacing: -1.6px; }
@mixin moreArrow() {
	width: 10px;
	height: 10px;
	border-top: 1px solid #c9c9c9;
	border-right: 1px solid #c9c9c9;
	transform: rotate(45deg); }

/************** Common ***************/
body {
	font-size: 20px;
	min-width: 1024px; }
#wrap {
	position: relative;
	&::before {
		content: "";
		background: url("../images/bg_stars.png");
		background-size: 25%;
		background-repeat: repeat;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: -1; } }

#content {
	margin: 0 auto;
	max-width: 100%;
	padding-top: 150px; }
.arrowBtn {
	display: block;
	margin: 0 auto;
	color: $white;
	margin-top: 30px;
	width: 211px;
	height: 56px;
	@include font20();
	line-height: 56px;
	background-color: $point;
	border-radius: 30px;
	padding-right: 20px;
	position: relative;
	&:after {
		content: "";
		width: 10px;
		height: 10px;
		border-top: 1px solid $white;
		border-right: 1px solid $white;
		position: absolute;
		top: 23px;
		right: 20%;
		transform: rotate(45deg); } }
.goBtn {
	display: block;
	margin: 0 auto;
	color: $white;
	margin-top: 30px;
	width: 218px;
	height: 56px;
	line-height: 56px;
	background-color: $bgColor02;
	border-radius: 30px;
	font-size: 16px;
	letter-spacing: -0.32px;
	font-family: 'Noto Sans KR bold';
	cursor: pointer; }
.goBtn02 {
	display: block;
	text-align: center;
	margin: 0 auto;
	color: $white;
	margin-top: 30px;
	border-radius: 30px;
	font-family: 'Noto Sans KR bold';
	width: 119px;
	background-color: $bgColor02;
	font-size: 14px;
	line-height: 36px;
	height: 36px;
	letter-spacing: -0.28px; }
.closeBtn {
	display: block;
	position: absolute;
	top: -58px;
	left: 50%;
	width: 40px;
	height: 40px;
	border-radius: 50%;
	background-color: $white;
	transform: translateX(-50%);
	text-indent: -9999px;
	&::before {
		content: "";
		width: 20px;
		height: 1px;
		background-color: $black;
		position: absolute;
		top: 20px;
		left: 10px;
		transform: rotate(-45deg); }
	&::after {
		content: "";
		width: 20px;
		height: 1px;
		background-color: $black;
		position: absolute;
		top: 20px;
		left: 10px;
		transform: rotate(45deg); } }
.subTitle {
	text-align: center;
	dt {
		@include font20(); }
	dd {
		@include colorGradation();
		@include font80();
		margin-top: 10px;
		display: inline-block; } }
.eventNotice {
	margin-top: 80px;
	padding: 40px 0;
	background-color: $bgColor03;
	h5 {
		margin: 0 auto;
		width: 712px;
		@include font14(); }
	ul {
		margin: 0 auto;
		width: 712px;
		margin-top: 10px;
		li {
			@include font14();
			line-height: 1.8;
			color: $lightGray;
			position: relative;
			padding-left: 10px;
			&::before {
				content: "";
				position: absolute;
				top: 12px;
				left: 0;
				width: 3px;
				height: 3px;
				border-radius: 50%;
				background-color: $lightGray; }
			span {
				color: $white; }
			a {
				color: $lightGray; } } } }
.alertWin {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.7);
	display: none;
	z-index: 50;
	&.on {
		display: block; }
	& > div {
		text-align: center;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translateX(-50%) translateY(-50%);
