		height: 240px;
		background-color: $white;
		border-radius: 30px;
		padding-top: 40px;
		dl {
			dt {
				@include colorGradation();
				font-size: 24px;
				line-height: 1.2;
				letter-spacing: -0.48; }
			dd {
				margin-top: 10px;
				font-size: 14px;
				letter-spacing: -0.28px;
				color: $black;
				font-family: 'Noto Sans KR bold'; } }
		div {
			display: flex;
			padding: 0 15px;
			.goBtn {
				width: 110px; }
			.arrowBtn {
				background-color: $lightGray;
				margin-left: 4px; } } } }
.cartBtn {
	margin: 0 auto;
	margin-top: 20px;
	width: 230px;
	height: 79px;
	position: relative;
	border-radius: 30px;
	display: block;
	color: $white;
	background: url("../images/upload.png") no-repeat 0 0;
	background-position: center 11px;
	padding: 48px 11px 11px;
	font-size: 14px;
	letter-spacing: -0.28px;
	line-height: 1.4;
	font-family: 'Noto Sans KR bold';

	span {
		text-indent: -9999px;
		@include borderGradient();
		z-index: -1;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		border-radius: 30px; } }
.userInfo {
	padding: 0 8% 63px 8%;
	margin-bottom: 50px;
	position: relative;
	&::after {
		content: "";
		width: 100%;
		height: 2px;
		position: absolute;
		bottom: 0;
		left: 0;
		background: linear-gradient(100deg, $frontColor, $backColor); }
	.olklPass {
		div {
			text-align: center;
			font-size: 14px;
			line-height: 1.4;
			letter-spacing: -0.28px;
			font-family: 'Noto Sans KR bold';
			&:first-child {
				margin-bottom: 10px; } } }

	h4 {
		margin-top: 30px;
		@include colorGradation();
		font-size: 24px;
		line-height: 1.6;
		letter-spacing: -0.48px;
		font-weight: bold;
		width: 100%;
		text-align: center; }
	.userInfo-input {
		margin-top: 10px;
		div {
			width: 100%;
			height: 54px;
			border-radius: 10px;
			margin-top: 31px;
			position: relative;
			strong {
				display: none; }
			em {
				font-size: 12px;
				color: $wrongColor;
				position: absolute;
				right: 12px;
				top: 16px; }
			&:first-child {
				margin-top: 0; }
			&.wrongValue {
				border: 1px solid $wrongColor;
				.effect-24:focus ~ .focus-bg:after,
				.has-content.effect-24 ~ .focus-bg::after,
				.effect-24 ~ .focus-bg::after,
				.effect-24:focus ~ .focus-bg::before,
				.has-content.effect-24 ~ .focus-bg::before {
					display: none; }
				.focus-bg {
					display: none; }
				strong {
					display: block;
					position: absolute;
					bottom: -22px;
					left: 0;
					color: $wrongColor;
					font-size: 12px;
					letter-spacing: -0.24px;
					line-height: 1.4; } }

			input {
				background-color: transparent;
				line-height: 54px;
				height: 54px;
				width: 100%;
				border-radius: 10px;
				padding-left: 20px;
				font-weight: bold;
				font-size: 14px;
				letter-spacing: -0.28px;
				color: $darkGray;
				position: relative;
				border: 0;
				z-index: 2; }

			.effect-24 {
				border: 0;
				padding: 7px 15px;
				position: relative;
				background: transparent; }
			.effect-24 ~ .focus-bg::before {
				content: "";
				position: absolute;
				left: 0px;
				top: -1px;
				border-radius: 10px;
				transition: 0.3s;
				padding: 1px;
				width: 99%;
				height: 50px;
				border: 1px solid $lightGray;
				box-sizing: content-box; }
			.effect-24 ~ .focus-bg::after {
				content: "";
				position: absolute;
				left: 50%;
				top: 50%;
				width: 0;
				height: 0;
				transition: 0.3s; }
			.effect-24:focus ~ .focus-bg:before,
			.has-content.effect-24 ~ .focus-bg::before {
				width: 100%;
				transition: 0.3s;
				left: -2px;
				top: -2px;
				height: 51px;
				@include borderGradient(); }
			.effect-24:focus ~ .focus-bg:after,
			.has-content.effect-24 ~ .focus-bg:after {
				transition: 0.3s;
				width: 100%;
				left: 0;
				top: 0;
				height: 100%;
				border-radius: 10px;
				background: linear-gradient(100deg, $frontColor, $backColor);
				opacity: 0.2; }
			.effect-24 ~ label {
				position: absolute;
				left: 14px;
				width: 100%;
				top: 15px;
				color: transparent;
				transition: 0.3s;
				z-index: -2;
				font-weight: bold;
				letter-spacing: -0.28px;
				font-size: 14px; }
			.effect-24:focus ~ label, .has-content.effect-24 ~ label {
				top: 2px;
				left: 15px;
				font-size: 12px;
				color: $lightGray;
				transition: 0.3s;
				z-index: 10; }
			& > a {
				display: block;
				height: 54px;
				line-height: 54px;
				padding-left: 20px;
				font-weight: bold;
				font-size: 14px;
				letter-spacing: -0.28px;
				border-radius: 10px;
				color: $darkGray; }
			.reissuance {
				cursor: pointer;
				position: absolute;
				bottom: -40px;
				right: 5px;
				font-size: 12px;
				letter-spacing: -0.24px;
				padding-right: 12px;
				&::after {
					content: "";
					position: absolute;
					top: 23px;
					right: 0;
					@include moreArrow(); } } }


		.telecom {
			.select {
				display: inline-block;
				width: 100%;
				height: 54px;
				line-height: 54px;
				position: relative;
				font-size: 14px;
				border-radius: 10px;
				&::before {
					content: "";
					position: absolute;
					left: 0px;
					top: -1px;
					border-radius: 10px;
					transition: 0.3s;
					padding: 1px;
					width: 99%;
					height: 50px;
					border: 1px solid $lightGray;
					box-sizing: content-box; }
				&::after {
					content: "";
					position: absolute;
					left: 50%;
					top: 50%;
					width: 0;
					height: 0;
					transition: 0.3s; }
				&.on {
					&::before {
						width: 100%;
						transition: 0.3s;
						left: -2px;
						top: -2px;
						height: 51px;
						@include borderGradient();
						border-bottom-left-radius: 0;
						border-bottom-right-radius: 0; }
					&::after {
						transition: 0.3s;
						width: 100%;
						left: 0;
						top: 0;
						height: 100%;
						border-radius: 10px;
						background: linear-gradient(100deg, $frontColor, $backColor);
						border-bottom-left-radius: 0;
						border-bottom-right-radius: 0;
						opacity: 0.2; } }
				&.radius {
					&::before {
						border-bottom-left-radius: 10px;
						border-bottom-right-radius: 10px; }
					&::after {
						border-bottom-left-radius: 10px;
						border-bottom-right-radius: 10px; } }

				& > .def {
					width: 100%;
					height: 54px;
					font-weight: bold;
					display: block;
					position: absolute;
					left: 0;
					top: 0;
					padding-left: 20px;
					color: $darkGray;
					font-size: 14px;
					z-index: 1; }

				& > .dropdown {
					height: 54px;
					width: 36px;
					display: block;
					position: absolute;
					right: 0;
					top: 0;
					color: $darkGray;
					font-size: 10px; }

				& > .options {
					width: 101.5%;
					position: absolute;
					top: 52px;
					left: -2px;
					text-align: center;
					border-radius: 3px;
					z-index: 10;
					& > li {
						border: 1px solid $lightGray;
						border-top: 0;
						background: $black;
						height: 54px;
						line-height: 54px;
						&:first-child {
							border-top: 1px solid $lightGray; }

						&:last-child {
							border-bottom-left-radius: 10px;
							border-bottom-right-radius: 10px; }
						& > a {
							display: block;
							color: $darkGray; } } } }
			select {
				display: none; } } }
	.numberIssuance {
		display: flex;
		.goBtn {
			display: none;
			width: 119px;
			text-align: center;
			background-color: $bgColor05;
