/************** Main ***************/
.mainVideo {
	div {
		width: 712px;
		overflow: hidden;
		background-color: #e5e5e5;
		margin: 0 auto;
		video {
			width: 100%;
			height: auto; } } }
.mainTab {
	padding-top: 51px;
	margin-top: 20px;
	margin-bottom: 160px;
	// tab Title
	& > ul {
		margin: 0 8%;
		display: flex;
		justify-content: space-around;
