				border-radius: 14px;
				background: $mainColor;
				transition: .3s;
				transform: translateX(0); } }
		&.moveRight {
			margin: 0 auto;
			width: 315px;
			&::before {
				content: "";
				position: absolute;
				top: 0;
				left: 0;
				width: 50%;
				height: 100%;
				border-radius: 14px;
				background: $mainColor;
				transition: .3s;
				transform: translateX(100%); } }
		li {
			width: 100%;
			a {
				display: block;
				height: 54px;
				padding-top: 16px;
				text-align: center;
				color: $white;
				position: relative;
				@include font16();
				font-family: 'Noto Sans KR bold'; }
			&.on {
				a {
					&:after {
						content: "";
						background: url("../images/icon_planet.png") no-repeat 0 0;
						position: absolute;
						top: -10px;
						left: 50%;
						transform: translateX(-50%);
						width: 30px;
						height: 24px;
						animation: opacity .3s both; } } } } }
	// tab Content
	.tabContent {
		margin-top: 60px;
		ul {
			.leftTab {
				& > dl {
					text-align: center;
					dt {
						@include font80();
						@include colorGradation(); }
					dd {
						margin-top: 30px;
						font-family: 'Noto Sans KR bold', sans-serif;
						@include font20(); } }
				em {
					@include font12();
					color: #fff;
					position: absolute;
					bottom: 59px;
					left: 50px; }
				.universe {
					margin-top: 90px;
					text-align: center;
					dl {
						dt {
							@include font20(); }
						dd {
							margin-top: 20px; } } }
				h5 {
					@include font80();
					@include colorGradation();
					text-align: center;
					margin-top: 130px; }
				ul {
					width: 712px;
					margin: 0 auto;
					margin-top: 30px;
					li {
						margin-top: 20px;
						width: 100%;
						border-radius: 20px;
						position: relative;
						&:first-child {
							margin-top: 0;
							a {
								&::after {
									content: "";
									position: absolute;
									top: 21px;
									right: 91px;
									width: 150px;
									height: 138px;
									background: url("../images/img-ticket-tmb-1.png") no-repeat 0 0;
									background-size: 100%; } } }
						a {
							padding: 30px 50px;
							display: block;
