footer {
	padding: 30px 0 47px 0;
	background-color: #000;
	& > div {
		width: 712px;
		margin: 0 auto;
		display: flex;
		justify-content: space-between;
		& > div {
			div {
				img {
					width: 127px;
					height: 41px; } }
			p {
				margin-top: 16px;
				@include font12();
				color: #ffffff; } }
		ul {
			display: flex;
			margin-top: 56px;
			li {
				&:first-child {
					position: relative;
					padding-right: 10px;
					margin-right: 10px;
					&:after {
						content: "";
						position: absolute;
						top: 1px;
						right: 0;
						width: 2px;
						height: 14px;
						background-color: #ffffff; } }
				a {
					@include font12();
					color: #ffffff; } } } } }
